@import url('https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,600;1,700;1,800&family=Ubuntu&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');

:root {
  --charcoal: #264653;
  --persianGreen: #2A9D8F;
  --crayola: #E9C46A;
  --siena: #E76F51;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Kanit', sans-serif;
  background: #fffbf5;
  color: var(--charcoal);
  max-width: 520px;
  margin: auto;
}
a {
  color: #212121;
  text-decoration: none;
}
.shadow {
  box-shadow: 0 0 0.3em rgb(0, 0, 0, 0.2);
}