nav {
  max-width: 520px;
  background: #212121;
  border-radius: 25px;
  position: fixed;
  width: 90%;
  gap: 5px;
  margin-left: 5%;
  bottom: 20px;
  padding: 17px;
  display: flex;
  gap: 12px;
  text-align: center;
  font-size: 14px;
  justify-content: center;
}
nav a {
  color: whitesmoke;
}
nav .fa-solid {
  color: whitesmoke;
  font-size: 22px;
  border-radius: 8px;
  border: 1px whitesmoke solid;
  padding: 10px;
  width: 45px;
  text-align: center;
}
nav .fa-solid:hover {
  background: #1db954;
  color: #fffbf5;
  border: none;
  padding: 11px;
}