.galery-container {
  padding: 15px 15px 450px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.galery-container .image-container {
  width: 180px;
  height: 180px;
  margin-top: 15px;
  aspect-ratio: 1/1;
  object-fit: cover;
  object-position: top;
  border-radius: 15px;
}
.image-viewer {
  position: fixed;
  display: flex;
  justify-content: center;
  justify-items: center;
  z-index: 11;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgb(0, 0, 0, 0.7);
  backdrop-filter: blur(3px);
}
.image-viewer img {
  width: 100%;
  border-radius: 15px;
}
.image-viewer .fa-solid {
  position: absolute;
  border: 1px #212121 solid;
  border-radius: 8px;
  background: #21212155;
  color: #212121;
  box-shadow: 0 0 0.3em rgb(0, 0, 0, 0.3);
}
.fa-arrow-left {
  left: 10px;
  top: 50%;
  padding: 10px 12px;
}
.fa-arrow-right {
  right: 10px;
  top: 50%;
  padding: 10px 12px;
}
.fa-x {
  right: 15px;
  top: 15px;
  padding: 5px 6px;
}