.home-container img {
  width: 100%;
  aspect-ratio: 9/16;
}
.home-container img {
  position: absolute;
  z-index: -10;
}
.hero-description {
  text-align: center;
  height: 720px;
  padding: 10% 0;
  width: 320px;
  margin: auto;
  color: #212121;
}
.hero-description h1 {
  margin: -30px 0 -30px;
}
.hero-name h2 {
  text-shadow: 0 0 0.3em rgb(0, 0, 0, 0.3);
}
.hero-name {
  margin-top: 10px;
  font-family: 'Great Vibes', cursive;
  font-size: 25px;
}
.invited-name {
  padding: 10px;
  background: whitesmoke;
  color: #212121;
  border-radius: 15px;
  margin-top: 300px;
}
.invited-name table {
  text-align: left;
  padding: 10px 0;
}
.invited-name th {
  padding: 0 5px;
}