.profile-container {
  text-align: center;
}
.profile-container img {
  width: 150px;
  border-radius: 50%;
  margin-top: 50px;
}
.h3 {
  font-family: 'Great Vibes', cursive;
  font-size: 28px;
  margin: 10px 0;
}
.profile-container .fa-solid {
  margin: 10px;
  font-size: 18px;
}
.button {
  padding: 8px 12px;
  font-weight: 500;
  border-radius: 8px;
  border: none;
  color: whitesmoke;
  background: #212121;
  margin: 10px 0;
}