.date-container {
  background-color: #212121;
  padding: 20px 0;
}
.date-event {
  background-color: whitesmoke;
  height: 60vh;
  width: 90%;
  margin: auto;
  background-size: cover;
  border-radius: 15px;
  box-shadow: 0 0 0.3em rgb(225,225,225, 0.4);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.date-event .fa-solid {
  margin: 5px;
}
li {
  list-style-type: none;
  text-align: center;
}